})

export default class HistoryTestList extends Vue {
  @State('historyExamList', { namespace: 'pastTest' }) stateHistoryExamList!: IHistoryExam[];
  @Action('getUploadFileUrl', { namespace: 'pastTest' }) getUploadFileUrl!: (params: IExamUrlParams) => Promise<void>;

  testItemList: IHistoryExam[] = [];

  link() {
    this.$router.push('/test/history');
  }

  created() {
    const temp: Promise<void>[] = [];
    this.stateHistoryExamList.forEach((d: IHistoryExam) => {
      if (d.flagQ === 1) {
        temp.push(this.getUploadFileUrl({
          // eslint-disable-next-line
          exam_no: d.id,
          type: '1',
        }));
      }
      if (d.flagA === 1) {
        temp.push(this.getUploadFileUrl({
          // eslint-disable-next-line
          exam_no: d.id,
          type: '2',
        }));
      }
    });

    Promise.all(temp)
      .then(() => {
        this.testItemList = this.stateHistoryExamList;
      });
  }

